import React, {useEffect} from 'react';
import TodoList from './todo/TodoList';
import Context from './context';
import Loader from './Loader'
import Confetti from './Confetti/Confetti';
import useLocalStorage from 'use-local-storage'
import { useTranslation, Trans } from "react-i18next";
import './App.css';


const AddTodo = React.lazy(
  () => new Promise(resolve => {
  setTimeout(() => {
    resolve(import('./todo/AddTodo'))
    }, 3000);
  })
)


export default function App() {

  const { t, i18n } = useTranslation();

  const changeLanguage = lng => {
    i18n.changeLanguage(lng);
  };


  const defaultDark = window.matchMedia('(prefers-color-scheme: dark)').matches;
  const [theme, setTheme] = useLocalStorage('theme', defaultDark ? 'dark' : 'light');

  const switchTheme = () => {
    const newTheme = theme === 'light' ? 'dark' : 'light';
    setTheme(newTheme);
  }

  const [todos, setTodos] = React.useState([])
  const [loading, setLoading] = React.useState(true)


  useEffect(() => {
        setTimeout(() => {
          const todos = [
            {id: 1, completed: true, title: 'Buy bread'},
            {id: 2, completed: false, title: 'Buy milk'},
            {id: 3, completed: false, title: 'Buy oil'},
          ]
          setTodos(todos)
          setLoading(false)
        }, 2000)
      
  }, [])


  function toggleTodo(id) {
    console.log(`todo id: ${id}`);

    setTodos(todos.map(todo => {
      if (todo.id === id) {
        todo.completed = !todo.completed;
      }
      return todo;
    })
    )
  }

  function addTodo(title) {
    setTodos(todos.concat([{
      title,
      id: Date.now(),
      completed: false
    }]))
  }

  function removeTodo(id) {
    setTodos(todos.filter(todo => todo.id !== id))
  }

  return (
    <div className="body-color" data-theme={theme}>
      <Context.Provider value={{ removeTodo }}>
        <div className="wrapper" >
          <div className="App-header">
          <button onClick={() => changeLanguage("de")}>de</button>
          <button onClick={() => changeLanguage("en")}>en</button>
          <button onClick={() => changeLanguage("ru")}>ru</button>
        </div>

          <h1>{t("ToDo")}</h1>
          <button onClick={switchTheme}>
            Switch to {theme === 'light' ? 'Dark' : 'Light'} Theme
          </button>

          <React.Suspense fallback={t("Loading...")}>
            <AddTodo onCreate={addTodo}/>
          </React.Suspense>

          {loading && <Loader />}
          {todos.length 
            ? (
              <TodoList todos={todos} onToggle={toggleTodo} /> 
            ): 
              loading ? null : (
                // <h2>List of todos is empty 🎉</h2>
                <div>
                  <Confetti></Confetti> 
                  <h2>
                    <Trans>
                      {t("List of todos is empty 🎉")}
                    </Trans>
                  </h2>
                </div>
              ) 
            }
        </div>
      </Context.Provider> 
    </div>
  )
}